import {Link} from "react-router-dom";
import { Flex, Spacer, Heading,Box } from '@chakra-ui/react'

const Navbar = ()=>{

    return (
        <>
       <Flex boxShadow='outline' pr={20} minWidth='max-content' className="Navbar" alignItems='center' gap='2'>
  <Box p='4' boxShadow='dark-lg'>
    <Heading  size='md'>Aditya Singh</Heading>
  </Box>
  <Spacer />
  <Box gap={10}>
  <Link className="Link" to="/">Home</Link>
  </Box>
  <Box>
       <Link to={"/About"}>About Me</Link>
  </Box>
  <Box>
       <Link  to={"#Skills"}>Skills</Link>
  </Box>
  <Box>
       <Link  to={"#Project"}>Project</Link>
  </Box>
  <Box>
       <Link  to={"#Contact"}>Contact</Link>
  </Box>
  <Box>
  <Link  to={"#Resume"}>Resume</Link>
  </Box>
  
</Flex>
        </>
    )
}
export default Navbar;