import { Heading, Box, Text } from "@chakra-ui/react";

const About = ()=>{
    return (
        <div>
           <Heading style={{color:"skyblue"}} mt ={65}>About Me</Heading> 
          <Box  alignContent="center" >
            <Text  color="grey" m={2} pl={200} pr={200}  textAlign="center">
            Aspiring Software development, adept (very good at)
engineering robust and user-centric solutions.
Collaborative, team player who is proficient in working
with interdisciplinary teams and executing goal-oriented
projects. Strongly interested in obtaining a Software
Developer position to work on enhancing the product
experience
 </Text>
         
          </Box>
        </div>
    )
}
export default About;